@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,700);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*::-moz-selection {
  color: black;
  background: #e4d5a6;
}

*::selection {
  color: black;
  background: #e4d5a6;
}

input:focus {
  outline: none;
}

html, body {
  min-height: 100vh;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: #ffeeb7;
  min-height: 100vh;
  width: 100%;
}

.container {
  -webkit-align-self: flex-start;
          align-self: flex-start;
  -webkit-transition: -webkit-flex 0.3s ease-in-out;
  transition: -webkit-flex 0.3s ease-in-out;
  transition: flex 0.3s ease-in-out;
  transition: flex 0.3s ease-in-out, -webkit-flex 0.3s ease-in-out;
  -webkit-flex: 0 0;
          flex: 0 0;
  overflow: hidden;
}

.move-up {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.app > h1 {
  text-align: center;
  display: block;
  max-width: 100vw;
}

.user-search {
  width: 80vw;
  max-width: 300px;
}

.user-search > input[type=text] {
  max-width: inherit;
  width: inherit;
  display: block;
  font-family: inherit;
  font-size: 1em;
  border: none;
  margin-top: 2em;
  padding: 0.5em 0 0.5em 0.3em;
  background-color: transparent;
}

.user-search::after {
  display: block;
  content: '';
  border-bottom: solid 4px black;
  -webkit-transition: -webkit-transform 0.2s ease-in-out;
  transition: -webkit-transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
  -webkit-transform: scaleX(0.4);
          transform: scaleX(0.4);
  -webkit-transform-origin: 0 50%;
          transform-origin: 0 50%;
}

.submitted::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

.user-search:focus-within::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

.error {
  background: black;
  color: #ffeeb7;
  margin-top: 1em;
  padding: 0.2em;
  font-size: 0.8em;
}

@-webkit-keyframes slide-up {
	0% {
		-webkit-transform: translateY(100%);
		        transform: translateY(100%)
	}

	100% {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
}

@keyframes slide-up {
	0% {
		-webkit-transform: translateY(100%);
		        transform: translateY(100%)
	}

	100% {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
}

.now-playing {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	width: 100%;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	padding: 1em;
	margin-top: 2em;
}

.song-details > h2 {
	margin: 0.6em;
}

.song-details {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-animation: slide-up ease 0.7s;
	        animation: slide-up ease 0.7s;
	margin-right: 4em;
}

.album-art {
	max-width: 400px;
}

.album-art > img {
	width: 100%;
	height: auto;
}

.info {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	max-width: 500px;
	-webkit-align-items: flex-start;
	        align-items: flex-start;
}

.info > p {
	color: #ffeeb7;
	background-color: black;
	padding: 0.4em;
	text-transform: lowercase;
	margin-bottom: 0.6em;
}

.lyrics {
	margin: 0.6em;
	max-width: 800px;
	-webkit-animation: slide-up ease 0.8s;
	        animation: slide-up ease 0.8s;
}

/* .lyric::selection, .lyric > br::selection {
	color: black;
	background: #ffeeb7;
}

.lyrics p::selection {
	color: black;
	background: #ffeeb7;
} */
.annotation {
	padding: 0.2em;
	max-width: 600px;
	line-height: 2em;
}

.lyric {
	position: relative;
	font-weight: bold;
}

.details {
	max-width: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-flow: row wrap;
	        flex-flow: row wrap;
	border: 4px solid;
	font-weight: 500;
	padding: 0.5em;
	color: #ffeeb7;
	background-color: black;
	margin: 8px 0.8em 0.5em 4px;
}

.details > * {
	-webkit-flex-grow: 1;
	        flex-grow: 1;
	-webkit-flex-basis: 100%;
	        flex-basis: 100%;
	width: 0;
}

.lyric-has-details {
	cursor: pointer;
	overflow: hidden;
	border: 4px solid black;
	margin-bottom: 0.5em;
	padding: 0.5em;
	box-shadow: 8px 8px black;
}

.annotation img {
	width: 100%;
	height: auto;
}

.annotation iframe {
	width: 100%;
	height: auto;
}

.annotation a {
	color: #ffeeb7;
	text-decoration: none;
	background-color: black;
	padding: 0 0.2em;
	overflow-wrap: break-word;
}

